.edit {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: calc((100vw - 85%) / 2);

  .field {
    width: 85%;
    margin-bottom: 20px;

    p {
      margin-bottom: 5px;
      font-size: 12px;
      color: rgba(255, 255, 255, 0.5);
    }

    input,
    textarea {
      width: 100%;
      padding: 6px;
      border: none;
      border-radius: 8px;
      background-color: rgba(35, 35, 35, 0.6);
      outline: none;
      font-size: 20px;
      resize: none;
    }
  }

  .save {
    width: 85%;
    height: 54px;
    text-align: center;
    line-height: 54px;
    font-size: 20px;
    background-color: rgb(35, 35, 35);
    border-radius: 20px;
  }
}
