.messages {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: calc((100vw - 85%) / 2);

  .message {
    display: flex;
    align-items: center;
    width: 85%;
    height: 54px;
    margin-bottom: 20px;
    background-color: rgb(35, 35, 35);
    border-radius: 20px;

    .avatar {
      width: 45px;
      height: 45px;
      margin-left: calc((54px - 45px) / 2);
      margin-right: 10px;
      background-size: cover;
      background-position: center;
      border-radius: 200px;
    }

    .preview-text {
      font-size: 12px;
      color: rgba(255, 255, 255, 0.7);
    }
  }
}
